.story-slant {
  background-position: center center;
  background-repeat: repeat;
  background-size: cover;
  height: 800px;
  overflow: hidden;

  &__image {
    bottom: 0;
    position: absolute;
    right: -45px;
  }

  &__slant {
    background-position: center center;
    background-repeat: repeat;
    background-size: cover;
    clip-path: polygon(0 0, 0% 100%, 100% 0);
    color: $white;
    height: 100%;
    padding-right: 20px;
    width: 100%;
  }

  &__title {
    padding-top: 20px;
  }

  &__slant-excerpt {
    margin-bottom: 20px;
    margin-top: 7px;
    max-width: 65%;
  }

  @media (min-width: $breakpoint-sm) {
    height: 1020px;

    &__title {
      padding-top: 60px;
    }

    &__slant-excerpt {
      max-width: 343px;
    }

    &__slant-excerpt-2 {
      // max-width: 340px;
    }
  }

  @media (min-width: $breakpoint-md) {
    &__image {
      right: 0;
    }
  }

  @media (min-width: $breakpoint-lg) {
    height: 800px;

    &__image {
      height: auto;
      width: auto;
    }

    &__slant {
      clip-path: polygon(0 0, 0% 100%, 80% 0);
    }

    &__slant-excerpt {
      margin-bottom: 30px;
      margin-top: 20px;
      max-width: 435px;
    }
  }

  @media (min-width: $breakpoint-xl) {
    height: 700px;
  }
}
