.section-padding {
  &-sm {
    padding-top: 34px;
    padding-bottom: 38px;
  }

  &-md {
    padding-top: 40px;
    padding-bottom: 50px;
  }

  &-lg {
    padding-top: 50px;
    padding-bottom: 45px;
  }

  @media (min-width: 768px) {
    &-md {
      padding-top: 90px;
      padding-bottom: 130px;
    }

    &-lg {
      padding-top: 120px;
      padding-bottom: 105px;
    }

    &-sm {
      padding-top: 42px;
      padding-bottom: 76px;
    }
  }
}

.bg {
  &--black {
    background-color: $black;
  }
}

.img-fluid {
  max-width: 100%;
  height: auto;
}

.h-100 {
  height: 100% !important;
}

.border {
  &-none {
    border: none !important;
  }
  &-top-none {
    border-top: none !important;
  }
  &-left-none {
    border-left: none !important;
  }
  &-right-none {
    border-right: none !important;
  }
  &-bottom-none {
    border-bottom: none !important;
  }
}

.text {
  &-center {
    text-align: center !important;
  }

  &-white {
    color: $white !important;
  }

  &-dark-gray {
    color: $dark-gray !important;
  }

  &-black {
    color: $black !important;
  }

  &-light-blue {
    color: $light-blue !important;
  }
}

.assistive-text {
  height: 1px;
  left: -10000px;
  overflow: hidden;
  position: absolute;
  top: auto;
  width: 1px;
}
