.button  {
  cursor: pointer;
  display: inline-block;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 14px;
  font-weight: bold;
  line-height: 18px;
  outline: none;
  padding: 12px 25px;
  position: relative;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.3s;

  &__border {
    &:before,
    &:after {
      border-style: solid;
      box-sizing: content-box;
      content: "";
      position: absolute;
      transition: all 0.3s;
      z-index: 5;
    }

    &:before {
      border-width: 1px 0 1px 0;
      height: 100%;
      left: 0;
      top: -1px;
      transition-delay: 0.05s;
      width: 0;
    }

    &:after {
      border-width: 0 1px 0 1px;
      height: 0;
      left: -1px;
      top: 0;
      width: 100%;
    }

    &:hover {
      background-color: transparent;

      &:before {
        width: 100%;
      }

      &:after {
        height: 100%;
      }
    }
  }

  &--black {
    background-color: $black;
    border: 1px solid transparent;
    color: $white;

    &:before,
    &:after {
      border-color: $dark-gray;
    }

    &:hover {
      background-color: $white;
      color: $dark-gray;
    }
  }

  &--white {
    background-color: $white;
    border: 1px solid transparent;
    color: $black;

    &:before,
    &:after {
      border-color: $white;
    }

    &:hover {
      background-color: $black;
      color: $white;
    }
  }

  @media (min-width: $breakpoint-md) {
    font-size: 16px;
    line-height: 20px;
    padding: 18px 62px;
  }
}


