.instagram-glide {
  &__button-link {
    padding: 16px 50px;
  }

  &__bullet-container {
    align-items: center;
    display: flex;
    justify-content: center;
  }
  &__bullet {
    background-color: #d0d0d0;
    border-radius: 50%;
    border: none;
    outline: none;
    padding: 5px;

    &:not(:first-of-type) {
      margin-left: 10px;
    }

    &.glide__bullet--active {
      background-color: #a9a9a9;
    }
  }

  &__container {
    .glide__track {
      cursor: grab;
    }
    .glide__slide {
      height: auto;
    }
    .glide--dragging .glide__track {
      cursor: grabbing;
    }
  }

  &__image {
    height: 100%;
    max-height: 500px;
    max-width: 100%;
    object-fit: cover;
    vertical-align: middle;
    width: 100%;
  }

  &__instagram-logo {
    height: 24px;
    left: 10px;
    position: absolute;
    top: 10px;
    transition: all 0.3s;
    width: 24px;
    z-index: 5;

    &--black {
      svg {
        fill: $black;
      }
    }
    &--white {
      svg {
        fill: $black;
      }
    }
  }

  &__product-title {
    color: $black;
    font-size: 1rem;
    font-style: italic;
    font-weight: 700;
    max-width: 80%;
    text-align: center;
    text-transform: uppercase;
  }

  &__slide {
    height: 100%;
    position: relative;
    width: 100%;
  }

  &__overlay {
    align-items: center;
    background-color: rgba(255, 255, 255, 0.4);
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: flex-end;
    left: 0;
    padding: 15px;
    position: absolute;
    top: 0;
    transition: all 0.3s;
    width: 100%;
  }

  @media (min-width: $breakpoint-lg) {
    &__container {
      margin-left: auto;
      margin-right: auto;
      max-width: 95%;
    }

    &__instagram-logo--white {
      svg {
        fill: $white;
      }
    }

    &__overlay {
      background-color: rgba(255, 255, 255, 0.6);
      justify-content: center;
      opacity: 0;
    }

    &__slide {
      &:hover {
        .instagram-glide__overlay {
          opacity: 1;
        }
        .instagram-glide__instagram-logo--white {
          svg {
            fill: $black;
          }
        }
      }
    }
  }
}
