.product-filter {
  &__btn {
    background-color: transparent;
    border: 1px solid $black;
    cursor: pointer;
    display: inline-block;
    font-family: inherit;
    font-size: 16px;
    font-weight: bold;
    outline: none;
    padding: 16px 60px;
    text-align: center;
    text-transform: uppercase;
    white-space: nowrap;

    @media (min-width: $breakpoint-lg) {
      display: none;
    }
  }

  &__btn-container {
    text-align: center;
  }

  &__body {
    border-bottom: 2px solid $dark-gray;
    border-top: 2px solid $dark-gray;
    padding: 25px 0;

    @media (min-width: $breakpoint-lg) {
      background-color: $grayish-blue;
      border-top: none;
      border-width: 1px;
      padding: 30px 0;
    }
  }

  &__label-text {
    color: $black;

    @media (min-width: $breakpoint-lg) {
      color: $white;
    }
  }

  &__checkbox {
    display: none;

    &:checked ~ .product-filter__checkmark {
      opacity: 1;
    }

    &:checked ~ .product-filter__label-text {
      color: $dark-gray;
      font-weight: bold;
    }
  }

  &__checkmark {
    color: $dark-gray;
    font-weight: bold;
    margin-left: 20px;
    opacity: 0;
    transition: all 0.3s;
  }

  &__container {
    background-color: $white;
    height: 0;
    left: 0;
    opacity: 0;
    overflow: hidden;
    position: fixed;
    top: 0;
    transition: all 0.3s;
    width: 100%;
    z-index: 101;

    &.active {
      height: 100vh;
      opacity: 1;
      overflow: scroll;
    }

    @media (min-width: $breakpoint-lg) {
      background-color: $black;
      height: auto;
      opacity: 1;
      position: relative;
      z-index: 10;
      overflow: visible;

      .arrow-button {
        display: none;
      }
    }
  }

  &__footer {
    border-bottom: 2px solid $dark-gray;
    padding: 15px 0;
    text-align: center;

    @media (min-width: $breakpoint-lg) {
      display: none;
    }
  }

  &__header {
    padding: 15px 0;

    @media (min-width: $breakpoint-lg) {
      padding: 15px 0 0;
    }
  }

  &__header-content {
    align-items: center;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    max-width: 100%;
    padding: 0 20px;

    @media (min-width: $breakpoint-sm) {
      max-width: 540px;
    }

    @media (min-width: $breakpoint-md) {
      max-width: 720px;
    }

    @media (min-width: $breakpoint-lg) {
      display: block;
      margin: 0;
      max-width: none;
      padding: 0;
    }
  }

  &__hidden-list {
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    transition: all 0.3s;

    &.active {
      max-height: 2000px;
      opacity: 1;
    }

    @media (min-width: $breakpoint-lg) {
      background-color: $black;
      height: auto;
      left: 0;
      opacity: 1;
      position: absolute;
      top: 100%;
      width: 100%;
      z-index: 200;

      &.active {
        border-bottom: 1px solid $dark-gray;
      }

      .product-filter__state-top-list {
        display: flex;
        justify-content: space-evenly;
        margin: 0 auto;
        max-width: 1110px;
        padding: 25px 0;

        .product-filter__hidden-list {
          border: none;
          max-height: 2000px;
          opacity: 1;
          position: static;
        }

        .product-filter__inner-list {
          display: block;
          padding-bottom: 0;

          &-li {
            &:not(:first-child) {
              margin-top: 10px;
              margin-left: 0;
            }
          }
        }

        .dropdown-button {
          cursor: default;
          color: $white;

          &.active {
            color: $black;
          }

          .plus-sign {
            display: none;
          }
        }

        &-li {
          &:not(:first-child) {
            margin-top: 0;
          }
        }
      }

      .product-filter__inner-list {
        display: flex;
        margin: 0 auto;
        justify-content: center;
        max-width: 930px;
        padding: 25px 0;

        &-li {
          &:not(:first-child) {
            margin-top: 0;
            margin-left: 30px;
          }
        }
      }
    }

    @media (min-width: 1200px) {
      .product-filter__inner-list {
        max-width: 1110px;
      }
    }
  }

  &__inner-list {
    list-style-type: none;
    margin: 15px 0 0;
    padding: 0 0 0 15px;

    &-li {
      &:not(:first-child) {
        margin-top: 15px;
      }
    }
  }

  &__label {
    cursor: pointer;
    position: relative;
    text-transform: uppercase;

    &-text {
      font-size: 14px;
      transition: all 0.3s;
    }
  }

  &__list {
    list-style-type: none;
    margin: 0;
    padding: 0;

    &-li {
      &:not(:first-child) {
        margin-top: 25px;
      }
    }
  }

  &__list-container {
    display: flex;
    justify-content: center;

    @media (min-width: $breakpoint-lg) {
      display: block;
    }
  }

  &__selected-items {
    display: none;
    font-size: 11px;
    line-height: 24px;
    margin-bottom: 0;
    margin-top: 8px;
    text-transform: uppercase;

    @media (min-width: $breakpoint-lg) {
      color: $white;
      display: block;
    }
  }

  &__state-top-list {
    list-style-type: none;
    margin: 15px 0 0;
    padding: 0 0 0 15px;

    &-li {
      &:not(:first-child) {
        margin-top: 15px;
      }
    }
  }

  &__submit-filter-btn {
    background-color: transparent;
    border: none;
    cursor: pointer;
    display: inline-block;
    font-family: inherit;
    font-size: 14px;
    font-weight: bold;
    line-height: 18px;
    outline: none;
    text-transform: uppercase;
    white-space: nowrap;
  }

  &__title {
    font-size: 14px;
    font-weight: bold;
    margin-left: 20px;

    @media (min-width: $breakpoint-lg) {
      color: $white;
      font-size: 24px;
      margin-left: 0;
      text-align: center;
    }
  }

  @media (min-width: $breakpoint-lg) {
    &__main-desktop-list {
      align-items: flex-start;
      display: flex;
      justify-content: center;

      .product-filter__list-li {
        &:not(:first-child) {
          margin-top: 0;
          margin-left: 60px;
        }
      }
    }
  }
}
