.about-page .content-container p img {
  margin-top: 30px;
  margin-bottom: 30px;
  width: 100% !important;
  height: auto !important;
}

section.about-page {
  margin-top: 50px;
}

.new-hero .wrapper {
  padding: 140px;
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.state-hero .wrapper {
  padding: 40px;
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.new-hero .hero-title,
.state-hero .hero-title {
  font-family: "Jost";
  text-shadow: 2px 2px 5px #000;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  border: 10px solid #fff;
  color: #fff;
  font-weight: 400;
  box-shadow: 2px 2px 5px #000, 2px 2px 5px #000 inset;
  word-break: keep-all;
  white-space: nowrap;
  max-width: max-content;
}

.state-hero .hero-title {
  color: #000;
  border: 10px solid #000;
}

.state-hero .hero-logo-container {
  max-height: 400px;
  max-width: 400px;
  height: auto;
  width: auto;
}

.hero-logo-container img {
  max-height: 140px;
  @media (min-width: $breakpoint-md) {
    padding-right: 25px;
  }
}


.hero-title {
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.5) 38%, transparent 50%);
  text-align: center;

  @media (min-width: 767px) {
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.5) 50%, transparent 50%);
  }


  &__left {
    padding: 15px 25px;
    padding-right: 35px;
    color: #000;
    background-color: #fff;
    mix-blend-mode: screen;
    font-weight: 600;
    text-shadow: none;
  }

  &__right {
    padding: 15px 25px;
  }
}

.state-hero .hero-title__left {
  color: #fff;
  background-color: #000;
  mix-blend-mode: multiply;
}

@media (max-width: 768px) {
  .hero-title__left,
  .hero-title__right {
    padding: 7px 15px;
  }

  .hero-title__left {
    padding-right: 20px;
  }
}

.new-hero {
  position: relative;

  .wrapper {
    position: relative;
  }
}

.backdrop {
  position: absolute;
  width: 100%;
  height: 100%;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
