.full-width-banner {
  height: 75vh;
  max-height: 650px;
  width: 100%;
  margin-top: -20px;

  &__img {
    height: 100%;
    object-fit: cover;
    width: 100%;
  }

  &__with-bottom-border {
    // border-bottom: 2px solid $dark-gray;
  }

  @media (min-width: 992px) {
    &__with-bottom-border {
      // border-bottom: 3px solid $dark-gray;
    }
  }

  &--cta {
    background-size: cover;
    background-position: 50% 50%;
    min-height: 50vw;
    display: flex;
    align-items: flex-end;
    justify-content: center;

    &__title-image {
      max-width: 100%;
      padding: 0 20px;
    }

    @media (min-width: $breakpoint-sm) {
      padding-bottom: 5vw;
    }
    @media (min-width: $breakpoint-md) {
      padding-bottom: 10vw;
    }
    @media (min-width: $breakpoint-lg) {
      padding-bottom: 5vw;
    }
    @media (min-width: $breakpoint-xl) {
      // padding-bottom: 15vw;
      padding-bottom: 5vw;
    }

    h1,
    h2 {
      color: $white;
      text-transform: none;
    }

    h2 {
      font-size: 1.25rem;
      text-shadow: 0px 0px 10px rgb(0, 0, 0);

      @media (max-width: $breakpoint-lg) {
        font-weight: 400;
      }
    }

    h1 {
      font-family: Nostalgic Script, cursive;
      font-size: 4rem;
      font-weight: normal;
      line-height: 1em;

      &.fancy-text {
        position: relative;

        &::after {
          -webkit-font-smoothing: antialiased;
          -webkit-text-fill-color: $black;
          -webkit-text-stroke: 8px $black;
          color: $black;
          height: 100%;
          left: 0;
          position: absolute;
          width: 100%;
          z-index: 2;
          // font-weight: bold;
          // text-shadow: -3px 6px 0 #000, 3px -1px 0 #000, -1px -2px 0 #000,
          //   -4px 3px 0 #000;
        }
      }
      .fancy-text-span {
        color: $white;
        position: relative;
        z-index: 3;
        // text-shadow: -6px 0px 0px $black, 0px 6px 0px $black, 6px 6px 0px $black;
        text-shadow: -8px 0px 0px $black;
        top: 0;
        // text-shadow: -3px 0px 0px $black;
      }

      @media (min-width: $breakpoint-sm) {
        font-size: 5rem;
      }
      @media (min-width: $breakpoint-md) {
        font-size: 8rem;
      }
    }

    .button--black {
      border: 1px solid $white;
    }
  }
}

