@import "variables";

.dropdown-button {
  background-color: transparent;
  border: none;
  color: $black;
  cursor: pointer;
  display: inline-block;
  font-family: inherit;
  font-size: 14px;
  font-weight: bold;
  outline: none;
  padding: 0;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  white-space: nowrap;

  &:disabled,
  &[disabled] {
    color: #c0c0c0;
    cursor: not-allowed;

    .plus-sign {
      &::after {
        background-color: #c0c0c0;
      }
    }

    &:hover {
      color: #c0c0c0;

      .plus-sign {
        background-color: #c0c0c0;

        &::after {
          background-color: #c0c0c0;
        }
      }
    }
  }

  &.active {
    color: $dark-gray;

    .plus-sign {
      background-color: $dark-gray;
      transform: rotate(180deg);

      &::after {
        background-color: $dark-gray;
      }
      &::after {
        opacity: 0;
      }
    }
  }

  .plus-sign {
    margin-left: 20px;
  }

  .active {
    color: $dark-gray;
  }

  &__body {
    align-items: center;
    display: flex;
  }

  &--lg {
    color: $dark-gray;
    font-size: 18px;
    transition: all 0.3s;

    .plus-sign {
      background-color: $dark-gray;

      &::after {
        background-color: $dark-gray;
      }
    }

    @media (min-width: $breakpoint-lg) {
      color: $white;

      &:hover {
        color: $dark-gray;

        .plus-sign {
          background-color: $dark-gray;

          &::after {
            background-color: $dark-gray;
          }
        }
      }

      .plus-sign {
        background-color: $white;

        &::after {
          background-color: $white;
        }
      }
    }
  }

  &__even-space {
    .dropdown-button__body {
      align-items: center;
      display: flex;
      justify-content: space-between;
      width: 160px;
    }

    @media (min-width: 992px) {
      .dropdown-button__body {
      }
    }
  }
}
