.split-hero {
  border-top: 5px solid $dark-gray;
  border-bottom: 5px solid $dark-gray;

  &__left-column {
    background-color: $black;
    color: $white;
    display: block;
    height: auto;
    padding: 28px 0;
    text-align: center;
    width: 100%;
  }

  &__left-column__inner {
    margin: 0 auto;
    max-width: 473px;
  }

  &__product-image {
    height: 145px;
    max-width: 100%;
  }

  &__right-column__image {
    display: block;
    max-height: 130px;
    max-width: 100%;
    object-fit: cover;
    width: 100%;
  }

  @media (min-width: $breakpoint-md) {
    display: flex;

    &__left-column {
      padding: 95px 0;
    }

    &__product-image {
      height: auto;
      max-width: 100%;
    }

    &__right-column {
      flex: 0 0 35%;
      order: 2;
    }

    &__right-column__image {
      height: 100%;
      max-height: none;
    }
  }
}
