.elfd-product-grid {
  display: grid;
  grid-template-columns: repeat(2, auto);
  justify-content: space-between;
  row-gap: 45px;
  column-gap: 20px;

  @media (min-width: 767px) {
    grid-template-columns: repeat(3, auto);
  }

  @media (min-width: 992px) {
    grid-template-columns: repeat(4, auto);
    row-gap: 40px;
  }
}
