.elfd-product {
  display: flex;
  flex-direction: column;
  height: 100%;
  text-align: center;

  &__name {
    font-size: 14px;
    font-style: normal;
    font-weight: normal;
    line-height: 18px;
    text-transform: uppercase;
  }

  &__image {
    max-height: 205px;
    max-width: 100%;
    object-fit: contain;
    vertical-align: middle;
    height: 100%;
  }

  &__overlay {
    display: none;
  }

  &__info {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__price {
    font-size: 14px;
    line-height: 18px;
    margin-top: 16px;
    text-transform: uppercase;
  }

  &__details-link {
    color: $black;
    display: inline-block;
    font-size: 12px;
    line-height: 18px;
    margin-bottom: 0;
    margin-top: 10px;
    text-decoration: none;
    text-transform: uppercase;
    transition: all 0.3s;

    &:hover {
      color: $dark-gray;
    }
  }

  &__style-count {
    color: $dark-gray;
    font-size: 12px;
    font-weight: bold;
    margin-top: 10px;
    text-transform: uppercase;
  }

  @media (min-width: 768px) {
    &__image {
      max-height: 380px;
    }
  }

  @media (min-width: 992px) {
    &:hover {
      .elfd-product__cart-btn {
        opacity: 1;
      }
    }

    &__cart-btn {
      margin-left: auto;
      margin-right: auto;
      max-width: 225px;
      opacity: 0;
      padding-left: 0;
      padding-right: 0;
      width: 100%;
    }

    &__details-link {
      display: none;
    }

    &__image {
      max-height: 420px;
    }

    &__name {
      font-size: 18px;
    }

    &__price {
      font-size: 18px;
    }

    &__overlay {
      background: linear-gradient(
        0deg,
        rgba(90, 110, 134, 0.6),
        rgba(90, 110, 134, 0.6)
      );
      align-items: center;
      display: flex;
      height: 100%;
      justify-content: center;
      left: 0;
      opacity: 0;
      position: absolute;
      top: 0;
      transition: all 0.3s;
      width: 100%;
    }

    &__overlay-details {
      color: $white;
      font-family: inherit;
      font-size: 24px;
      font-weight: normal;
      line-height: 32px;
      margin-top: 60px;
      text-decoration: none;
      text-transform: uppercase;
      transition: all 0.3s;

      &:hover {
        color: $black;
      }
    }

    &__overlay-container {
      flex: 1;
      position: relative;
      text-align: center;
      z-index: 5;

      &:hover {
        .elfd-product__overlay {
          opacity: 1;
        }
      }
    }

    &__style-count {
      font-size: 14px;
    }
  }
}

.product-image-container {
  margin: 0 auto;
  width: 80%;
}
