.product-showcase {
  &__body {
    bottom: 20px;
    left: 50%;
    position: absolute;
    text-align: center;
    transform: translateX(-50%);
    width: 100%;
  }

  &__image {
    height: 100%;
    object-fit: cover;
    width: 100%;
  }

  &__item {
    height: 202px;
    position: relative;
    width: 100%;
  }

  &__link-wrapper {
    display: block;
    height: 100%;
    text-decoration: none;
    width: 100%;
  }

  &__title {
    color: rgba(255, 255, 255, 0.9);
    font-size: 36px;
    font-style: italic;
    line-height: 46px;
    margin-bottom: 12px;    
    text-shadow: 0 0 10px $black;
  }

  @media (min-width: $breakpoint-md) {
    display: flex;
    flex-wrap: wrap;

    &__item {
      flex: 0 0 50%;
    }
  }

  @media (min-width: $breakpoint-lg) {
    &__item {
      flex: 1;
      height: 327px;
      transition: all 0.3s ease-in-out;

      .product-showcase__button {
        opacity: 0;
        transition: all 0.3s ease-in-out;
      }

      &:hover {
        flex: 2;

        .product-showcase__title,
        .product-showcase__button {
          opacity: 1;
        }
      }
    }
  }
}
