.iframe {
  border: none;
  height: 222px;
  width: 100%;

  @media (min-width: $breakpoint-md) {
    height: 444px;
  }
}

.iframeVimeoVideoWrapper {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  height: 0;
}
.iframeVimeoV iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
