body,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-family: "Roboto Condensed", sans-serif;
}

h1,
.hero-title {
  font-size: 36px;
  font-weight: bold;
  line-height: 46px;
  text-transform: uppercase;

  @media (min-width: $breakpoint-md) {
    font-size: 48px;
  }
}

.collections-hero-title {
  font-size: 24px;
  font-weight: bold;
  line-height: 32px;
  text-transform: uppercase;

  @media (min-width: $breakpoint-md) {
    font-size: 48px;
  }
}

h2 {
  font-size: 24px;
  font-weight: bold;
  line-height: 33px;
  text-transform: uppercase;

  @media (min-width: $breakpoint-md) {
    font-size: 36px;
    line-height: 46px;
  }
}

h3 {
  font-size: 24px;
  font-style: italic;
  font-weight: bold;
  line-height: 36px;
  text-transform: uppercase;

  @media (min-width: $breakpoint-md) {
    font-size: 36px;
  }
}

h4 {
  font-size: 18px;
  font-style: italic;
  font-weight: bold;
  line-height: 20px;
  text-transform: uppercase;

  @media (min-width: $breakpoint-md) {
    font-size: 24px;
    line-height: 36px;
  }
}

h5 {
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;

  @media (min-width: $breakpoint-md) {
    font-size: 18px;
    line-height: 26px;
  }
}

p {
  font-size: 14px;
  line-height: 18px;

  @media (min-width: $breakpoint-md) {
    font-size: 16px;
    line-height: 24px;
  }
}

.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
