.featured-sale {
  margin-top: 50px;
  margin-bottom: 80px;

  &__col-1 {
    flex: 0 0 75%;
  }

  &__col-2 {
    flex: 25%;
  }

  &__images-container {
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
  }

  &__images {
    align-items: flex-end;
    display: flex;
    justify-content: center;
    margin-bottom: -80px;
    margin-left: 12px;
  }

  &__image-large,
  &__image-small {
    height: auto;
    max-width: 100%;
    object-fit: cover;
    width: 100%;
  }

  &__image-large {
    max-height: 185px;
  }

  &__image-small {
    max-height: 100px;
    margin-left: -12px;
  }

  &__fancy-border {
    border: 5px solid $black;
    padding: 8px;
  }

  &__fancy-border-inner {
    border: 2px solid $black;
  }

  &__fancy-border-content {
    margin-left: auto;
    margin-right: auto;
    margin-top: -50px;
    max-width: 70%;
    position: relative;
    text-align: center;

    h1,
    h2,
    p,
    a {
      position: relative;
      z-index: 2;
    }

    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 100%;

      height: 100px;
      background-color: #fff;
      z-index: 1;
      clip-path: polygon(10% 0%, 90% 0%, 100% 100%, 0% 100%);

      @media (min-width: $breakpoint-md) {
        clip-path: polygon(20% 0%, 80% 0%, 100% 100%, 0% 100%);
      }
    }
  }

  @media (min-width: $breakpoint-md) {
    &__image-large {
      max-height: 400px;
    }

    &__image-small {
      max-height: 185px;
    }

    &__images-container {
      max-width: 85%;
    }

    &__fancy-border-content {
      max-width: 60%;
    }

    &__images {
      margin-left: 37px;
    }

    &__image-small {
      margin-left: -37px;
    }
  }
}
