.hat-swatch {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 30px;
  position: relative;

  &__btn-list {
    align-items: center;
    display: grid;
    flex: 1;
    justify-content: center;
    list-style-type: none;
    margin: 0;
    padding: 0;
    row-gap: 10px;
    width: 100%;

    li {
      text-align: center;
    }
  }

  &__container {
    margin-left: auto;
    margin-right: auto;
    max-width: 540px;

    @media (min-width: $breakpoint-md) {
      max-width: 720px;
    }

    @media (min-width: $breakpoint-lg) {
      max-width: 960px;
    }

    @media (min-width: 1400px) {
      max-width: 1280px;
    }
  }

  &__grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    .hat-swatch {
      flex: 0 0 100%;

      &:not(:first-of-type) {
        margin-top: 55px;
      }
    }

    @media (min-width: $breakpoint-md) {
      .hat-swatch {
        flex: 0 0 50%;

        &:not(:first-of-type) {
          margin-top: 0;
        }
        &:nth-child(n + 3) {
          margin-top: 55px;
        }

        &:not(:first-child) {
          &::before {
            background-color: $black;
            content: "";
            height: 100%;
            left: 0px;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            width: 2px;
          }
        }
        &:nth-child(2n + 1) {
          &::before {
            display: none;
          }
        }
      }
    }

    @media (min-width: $breakpoint-lg) {
      .hat-swatch {
        flex: 0 0 calc(100% / 3);

        &:nth-child(n + 3) {
          margin-top: 0px;
        }

        &:nth-child(n + 4) {
          margin-top: 50px;
        }

        &:nth-child(2n + 1) {
          &::before {
            display: block;
          }
        }
        &:nth-child(3n + 1) {
          &::before {
            display: none;
          }
        }
      }
    }

    @media (min-width: 1400px) {
      .hat-swatch {
        flex: 0 0 25%;

        &:nth-child(n + 3) {
          margin-top: 0px;
        }

        &:nth-child(n + 5) {
          margin-top: 50px;
        }

        &:nth-child(2n + 1),
        &:nth-child(3n + 1) {
          &::before {
            display: block;
          }
        }

        &:nth-child(4n + 1) {
          &::before {
            display: none;
          }
        }
      }
    }
  }

  &__link {
    margin-top: 30px;
    text-align: center;
  }

  &__product-title {
    color: $black;
    display: inline-block;
    font-size: 36px;
    margin-bottom: 20px;
    position: relative;
    text-align: center;

    span {
      color: $black;
      display: block;
      font-size: 16px;
      font-style: italic;
      position: absolute;
      right: 0;
      text-align: right;
      top: calc(100% - 1.2em);
    }
  }

  &__product-image-container {
    margin-bottom: 15px;
    margin-top: 15px;
    text-align: center;
  }

  &__product-image {
    height: 200px;
    object-fit: contain;
    width: 100%;
  }

  &__swatch {
    -webkit-appearance: none;
    border-radius: 50%;
    border: 2px solid $black;
    cursor: pointer;
    height: 30px;
    outline: none;
    overflow: hidden;
    padding: 0;
    position: relative;
    transition: all 0.3s;
    white-space: nowrap;
    width: 30px;

    &:hover {
      transform: scale(1.05);
    }

    &--two-colors {
      &::after {
        background-color: $black;
        content: "";
        height: 2px;
        left: 0;
        position: absolute;
        top: 50%;
        transform: translateY(-50%) rotate(90deg);
        width: 100%;
      }
    }

    &.hat-swatch__swatch--two-colors {
      transform: rotate(45deg);

      &:hover {
        transform: rotate(45deg) scale(1.05);
      }
    }
  }
}
