.button {
  cursor: pointer;
  display: inline-block;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 14px;
  font-weight: bold;
  line-height: 18px;
  outline: none;
  padding: 12px 25px;
  position: relative;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.3s;
}

.button__border:before, .button__border:after {
  border-style: solid;
  box-sizing: content-box;
  content: "";
  position: absolute;
  transition: all 0.3s;
  z-index: 5;
}

.button__border:before {
  border-width: 1px 0 1px 0;
  height: 100%;
  left: 0;
  top: -1px;
  transition-delay: 0.05s;
  width: 0;
}

.button__border:after {
  border-width: 0 1px 0 1px;
  height: 0;
  left: -1px;
  top: 0;
  width: 100%;
}

.button__border:hover {
  background-color: transparent;
}

.button__border:hover:before {
  width: 100%;
}

.button__border:hover:after {
  height: 100%;
}

.button--black {
  background-color: #000;
  border: 1px solid transparent;
  color: #fff;
}

.button--black:before, .button--black:after {
  border-color: #808080;
}

.button--black:hover {
  background-color: #fff;
  color: #808080;
}

.button--white {
  background-color: #fff;
  border: 1px solid transparent;
  color: #000;
}

.button--white:before, .button--white:after {
  border-color: #fff;
}

.button--white:hover {
  background-color: #000;
  color: #fff;
