}

.hat-swatch__grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.hat-swatch__grid .hat-swatch {
  flex: 0 0 100%;
}

.hat-swatch__grid .hat-swatch:not(:first-of-type) {
  margin-top: 55px;
}

@media (min-width: 768px) {
  .hat-swatch__grid .hat-swatch {
    flex: 0 0 50%;
  }
  .hat-swatch__grid .hat-swatch:not(:first-of-type) {
    margin-top: 0;
  }
  .hat-swatch__grid .hat-swatch:nth-child(n + 3) {
    margin-top: 55px;
  }
  .hat-swatch__grid .hat-swatch:not(:first-child)::before {
    background-color: #000;
    content: "";
    height: 100%;
    left: 0px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 2px;
  }
  .hat-swatch__grid .hat-swatch:nth-child(2n + 1)::before {
    display: none;
  }
}

@media (min-width: 992px) {
  .hat-swatch__grid .hat-swatch {
    flex: 0 0 calc(100% / 3);
  }
  .hat-swatch__grid .hat-swatch:nth-child(n + 3) {
    margin-top: 0px;
  }
  .hat-swatch__grid .hat-swatch:nth-child(n + 4) {
    margin-top: 50px;
  }
  .hat-swatch__grid .hat-swatch:nth-child(2n + 1)::before {
    display: block;
  }
  .hat-swatch__grid .hat-swatch:nth-child(3n + 1)::before {
    display: none;
  }
}

@media (min-width: 1400px) {
  .hat-swatch__grid .hat-swatch {
    flex: 0 0 25%;
  }
  .hat-swatch__grid .hat-swatch:nth-child(n + 3) {
    margin-top: 0px;
  }
  .hat-swatch__grid .hat-swatch:nth-child(n + 5) {
    margin-top: 50px;
  }
  .hat-swatch__grid .hat-swatch:nth-child(2n + 1)::before, .hat-swatch__grid .hat-swatch:nth-child(3n + 1)::before {
    display: block;
  }
  .hat-swatch__grid .hat-swatch:nth-child(4n + 1)::before {
    display: none;
  }
}

.hat-swatch__link {
  margin-top: 30px;
  text-align: center;
}

.hat-swatch__product-title {
  color: #000;
  display: inline-block;
  font-size: 36px;
  margin-bottom: 20px;
  position: relative;
  text-align: center;
}

.hat-swatch__product-title span {
  color: #000;
  display: block;
  font-size: 16px;
  font-style: italic;
  position: absolute;
  right: 0;
  text-align: right;
  top: calc(100% - 1.2em);
}

.hat-swatch__product-image-container {
  margin-bottom: 15px;
  margin-top: 15px;
  text-align: center;
}

.hat-swatch__product-image {
  height: 200px;
  object-fit: contain;
  width: 100%;
}

.hat-swatch__swatch {
  -webkit-appearance: none;
  border-radius: 50%;
  border: 2px solid #000;
  cursor: pointer;
  height: 30px;
  outline: none;
  overflow: hidden;
  padding: 0;
  position: relative;
  transition: all 0.3s;
  white-space: nowrap;
  width: 30px;
}

.hat-swatch__swatch:hover {
  transform: scale(1.05);
}

.hat-swatch__swatch--two-colors::after {
  background-color: #000;
  content: "";
  height: 2px;
  left: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%) rotate(90deg);
  width: 100%;
}

.hat-swatch__swatch.hat-swatch__swatch--two-colors {
  transform: rotate(45deg);
}

.hat-swatch__swatch.hat-swatch__swatch--two-colors:hover {
  transform: rotate(45deg) scale(1.05);
}

footer.site-footer .grid {
  display: flex;
  justify-content: center;
}

.hide-overflow {
  overflow: hidden;
}
