// because chrome
.glide__slides {
  margin-bottom: 0;
  margin-top: 0;
}

.homepage-slider {
  background-size: 45vh auto;
  background-position: 50% 0%;
  background-repeat: no-repeat;

  @media (min-width: $breakpoint-sm) {
    background-size: cover;
    background-position: 100% 50%;
  }

  .image-container {
    width: 100%;
    height: 401px;

    @media (min-width: $breakpoint-md) {
      height: 100%;
    }
    background-size: cover;
    background-position: 100% 50%;
    position: absolute;
    top: 0;
  }

  .glide__track {
    position: relative;

    &:before {
      content: "";
      position: absolute;
      left: 0;
      top: -150px;
      width: 100%;
      height: 100%;
      background-color: white;
      transition: clip-path 0.4s;

      // clip-path: polygon(100% 0, 100% 0, 100% 0);
      clip-path: polygon(0 0px, 0px 0%, 100% 0, 100% 0px);

      z-index: 1;

      @media (min-width: $breakpoint-md) {
        top: 0;
        clip-path: polygon(100% 0, 100% 0, 100% 0);
      }

      @media (min-width: $breakpoint-lg) {
        clip-path: polygon(100% 0, 100% 0, 100% 0);
      }
    }

    &:after {
      content: "";
      position: absolute;
      left: 0;
      top: 150px;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: white;
      transition: clip-path 0.2s;
      // clip-path: polygon(0 0, 100% 200px, 100% 100%, 0 100%);
      clip-path: polygon(0 15%, 100% 250px, 100% 100%, 0 100%);

      @media (min-width: $breakpoint-sm) {
        clip-path: polygon(0 20%, 100% 250px, 100% 100%, 0 100%);
      }

      @media (min-width: $breakpoint-md) {
        // clip-path: polygon(0 0, 100% 50%, 100% 100%, 0% 100%);
        clip-path: polygon(10% 0, 100% 120%, 100% 100%, 0 100%, 0 0);
        top: 0;
      }

      @media (min-width: $breakpoint-lg) {
        clip-path: polygon(10% 0, 100% 90%, 100% 100%, 0 100%, 0 0);
      }

      @media (min-width: $breakpoint-xl) {
        clip-path: polygon(10% 0, 90% 100%, 100% 100%, 0 100%, 0 0);
      }

      z-index: 1;
    }
  }

  &.transition,
  &.loading {
    .glide__track {
      overflow: visible;
      @media (min-width: $breakpoint-md) {
        overflow: hidden;
      }
    }
    // .glide__slides {
    //   overflow: hidden;
    //   // @media (min-width: $breakpoint-sm) {
    //   //   overflow: hidden;
    //   // }
    // }
    .glide__track:before {
      // clip-path: polygon(0 0, 100% 200px, 100% 0);
      // clip-path: polygon(0 0, 100% 251px, 100% 0);
      // clip-path: polygon(0 0px,0px 21%, 100% 251px,100% 0px);
      clip-path: polygon(0 0, 0px 50%, 100% 401px, 100% 0);

      // @media (min-width: $breakpoint-sm) {
      //   // clip-path: polygon(0 0, 100% 50%, 100% 0);

      // }

      // clip-path: polygon(0 15%,100% 251px,100% 100%,0 100%);

      // @media (min-width: $breakpoint-sm) {
      //   clip-path: polygon(0 20%,100% 251px,100% 100%,0 100%);
      // }

      // @media (min-width: $breakpoint-md) {
      //   // clip-path: polygon(0 0, 100% 50%, 100% 100%, 0% 100%);
      //   clip-path: polygon(10% 0, 100% 120%, 100% 100%, 0 100%, 0 0);
      // }

      @media (min-width: $breakpoint-md) {
        // clip-path: polygon(100% 0,0% 0%,100% 100%);
        // clip-path: polygon(0 0px,0px 0%, 100% 0,100% 120%)
        clip-path: polygon(100% 0, 0% 0%, 100% 120%);
      }

      @media (min-width: $breakpoint-lg) {
        clip-path: polygon(100% 0, 0% 0%, 100% 120%);
        // clip-path: polygon(10% 0, 90% 100%, 100% 100%, 0 100%, 0 0);
      }
    }
    .glide__slide--active.glide__slide:before {
      transition: none;
    }
  }

  // @media (min-width: 992px) {
  //   &.transition,
  //   &.loading {
  //     .glide__track:before {
  //       // clip-path: polygon(100% 0, 27% 0, 100% 100%);
  //       clip-path: polygon(100% 0, 9% 0, 100% 100%);
  //     }
  //   }
  // }
}

.homepage-slider {
  // min-height: 488px;
  // height: 90vw;
  // position: relative;

  min-height: 488px;
  height: 700px;
  position: relative;
  padding-top: 150px;

  @media (min-width: $breakpoint-md) {
    padding-top: 0;
  }

  &__bar {
    background-color: $black;
    display: flex;
    height: 5px;
    position: relative;
  }

  .glide__slides {
    position: relative;
    z-index: 2;
  }

  &__slide-number {
    color: $dark-gray;
    margin-bottom: 10px;
  }

  &__slide-name {
    margin-bottom: 8px;
    max-width: 256px;
  }

  &__slide-description {
    margin-bottom: 18px;
  }

  &__slide-body {
    max-width: 435px;
    position: relative;
    z-index: 2;

    @media (min-width: $breakpoint-md) and (max-width: $breakpoint-lg) {
      padding-right: 15vw;
    }
  }

  &__button-container {
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }

  &__button-prev,
  &__button-next {
    background-color: transparent;
    border: none;
    cursor: pointer;
    outline: none;
    padding: 0;

    svg {
      fill: $dark-gray;
      transition: all 0.3s linear;
    }

    &:hover {
      svg {
        fill: $light-blue;
      }
    }
  }

  .position-relative {
    position: relative;
  }

  &__controls-container {
    bottom: 0;
    left: 0;
    margin-bottom: 45px;
    // max-width: calc(100% - 20vw);
    max-width: 100%;
    @media (min-width: $breakpoint-md) {
      // max-width: calc(100% - 10vw);
      max-width: 80%;
    }
    @media (min-width: $breakpoint-lg) {
      // max-width: calc(100% - 20vw);
      max-width: 75%;
    }
    @media (min-width: $breakpoint-xl) {
      // max-width: calc(100% - 5vw);
    }
    padding: 0 20px;
    position: absolute;
    width: 100%;
    z-index: 3;
  }

  &__indicator-button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    flex: 1;
    outline: none;
    position: relative;
  }

  &__progress-bar {
    background-color: $dark-gray;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    transition: all 0.3s linear;
  }

  @media (min-width: $breakpoint-lg) {
    height: 50vw;

    &__controls-container {
      left: auto;
      padding: 0;
    }

    &__slide-number {
      margin-bottom: 20px;
    }

    &__slide-name {
      margin-bottom: 20px;
      max-width: none;
    }

    &__slide-description {
      margin-bottom: 32px;
    }
  }
}
