.plus-sign {
  background-color: $black;
  display: block;
  height: 1px;
  position: relative;
  transition: all 0.3s;
  width: 9px;

  &::after {
    background-color: $black;
    content: "";
    height: 1px;
    left: 0;
    opacity: 1;
    position: absolute;
    top: 0;
    transform: rotate(90deg);
    transition: all 0.3s;
    width: 9px;
  }
}
