.mobile-nav-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  height: 20px;
  justify-content: space-between;
  outline: none;
  padding: 0;
  width: 30px;

  span {
    background-color: #5a6e86;
    display: block;
    height: 3px;
    position: relative;
    transition: all 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
    width: 100%;
  }

  span:nth-child(2):after {
    background-color: #5a6e86;
    content: "";
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
  }

  &.active {
    span:nth-child(2) {
      transform: rotate(45deg);
    }

    span:nth-child(2):after {
      transform: rotate(-90deg);
    }

    span:first-child,
    span:last-child {
      opacity: 0;
      width: 0;
    }
  }

  @media (min-width: 992px) {
    display: none;
  }
}
