.header.util {
  position: relative;
  z-index: 100;
}

.header {
  &__top-nav {
    border-bottom: 2px solid $dark-gray;
    padding: 10px 0;
  }

  &__top-nav-list {
    align-items: center;
    display: flex;
    justify-content: space-between;
    list-style-type: none;
    margin: 0;
    padding: 0;
  }

  @media (min-width: 992px) {
    &__top-nav {
      border-bottom: none;
    }
  }
}

.header__logo {
  display: inline-block;

  img {
    height: 53px;
    max-width: 100%;
    vertical-align: middle;
    width: 77px;
  }

  &:hover {
    opacity: 0.8;
  }

  @media (min-width: 992px) {
    img {
      height: 70px;
      width: 100px;
    }
  }
}

.header__link {
  color: $black;
  cursor: pointer;
  font-family: "Roboto Condensed", sans-serif;
  font-family: inherit;
  font-size: 14px;
  font-weight: bold;
  line-height: 18px;
  outline: none;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.3s;

  &--normal {
    font-weight: normal;
  }

  &:hover {
    color: $dark-gray;
  }
}

.header__dropdown-btn {
  @media (min-width: 992px) {
    &-with-border {
      border-bottom: 2px solid $dark-gray;
      display: block;
      padding-bottom: 8px;
      position: relative;

      .plus-sign {
        display: none;
      }

      &::before,
      &::after {
        background-color: $dark-gray;
        content: "";
        height: 2px;
        position: absolute;
        top: 100%;
        width: 100%;
      }

      &::before {
        right: 100%;
      }

      &::after {
        left: 100%;
      }
    }
  }
}

.header__main-nav {
  height: 0;
  opacity: 0;
  overflow: hidden;
  transition: all 0.2s linear;

  &.active {
    height: calc(100vh - 73px);
    opacity: 1;
    overflow: scroll;
  }

  @media (min-width: 992px) {
    border-bottom: 3px solid $dark-gray;
    height: auto;
    opacity: 1;
    overflow: auto;
  }
}

.header__list {
  list-style-type: none;
  margin: 0;
  padding: 20px 0;

  &--small {
    list-style-type: none;
    margin: 0;
    padding: 20px 0 10px 20px;
  }

  &-li {
    &:not(:first-child) {
      margin-top: 20px;
    }
  }

  &-li-small {
    &:not(:first-child) {
      margin-top: 10px;
    }
  }
}

.header__footer-nav {
  border-top: 2px solid $dark-gray;
}

.header__search {
  &-bar {
    align-items: center;
    border: 1px solid transparent;
    display: flex;
    position: relative;
    transition: all 0.2s linear;

    &:before,
    &:after {
      border-style: solid;
      box-sizing: content-box;
      content: "";
      position: absolute;
      transition: all 0.3s;
      z-index: 5;
    }

    &:before {
      border-width: 1px 0 1px 0;
      height: 100%;
      left: 0;
      top: -1px;
      transition-delay: 0.05s;
      width: 0;
    }

    &:after {
      border-width: 0 1px 0 1px;
      height: 0;
      left: -1px;
      top: 0;
      width: 100%;
    }

    &.active {
      &:before {
        border-color: $dark-gray;
        width: 100%;
      }

      &:after {
        border-color: $dark-gray;
        height: 100%;
      }

      .header__search-btn {
        opacity: 1;
      }
    }
  }

  &-btn {
    background-color: transparent;
    border: none;
    cursor: pointer;
    outline: none;
    padding: 0;
    position: relative;
    text-align: center;
    transition: all 0.2s linear;
    white-space: none;
    z-index: 6;
  }

  &-container {
    border-bottom: 2px solid $dark-gray;
    padding: 15px 0;
  }

  &-icon {
    height: 16px;
    max-width: 100%;
    vertical-align: middle;
    width: 16px;
  }

  &-input {
    border: none;
    flex: 1;
    font-family: inherit;
    font-size: 14px;
    margin-left: 10px;
    margin-right: 10px;
    outline: none;
    padding: 0;
    position: relative;
    z-index: 6;

    &::placeholder {
      color: $black;
      font-weight: bold;
      text-transform: uppercase;
    }
  }

  @media (min-width: 992px) {
    &-bar {
      padding: 8px 16px 8px 8px;
    }

    &-btn {
      opacity: 0;
    }
  }
}

.header__hidden-nav {
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  transition: all 0.3s;

  &.active {
    max-height: 300vh;
    opacity: 1;
  }

  @media (min-width: 992px) {
    &.active {
      max-height: 800px;
      opacity: 1;

      [data-nested-nav="true"] {
        max-height: 800px;
        opacity: 1;
      }
    }
  }
}

@media (min-width: 992px) {
  .header__desktop-list {
    align-items: center;
    display: flex;
    justify-content: space-evenly;

    .header__list-li {
      &:not(:first-child) {
        margin-top: 0;
      }
    }
  }

  .header__list-desktop-container {
    margin-left: auto;
    margin-right: auto;
    max-width: 992px;

    .header__list--small {
      align-items: flex-start;
      display: flex;
      justify-content: space-evenly;
      padding: 30px 0;

      .header__hidden-nav {
        display: block;

        .header__list--small {
          display: block;
          padding-bottom: 0;
          padding-top: 25px;

          .header__list-li-small {
            &:not(:first-child) {
              margin-top: 10px;
            }
          }

          .header__link {
            font-weight: bold;
          }
        }
      }

      .header__list-li-small {
        &:not(:first-child) {
          margin-top: 0;
        }
      }
    }
  }

  .header__desktop-hidden-nav {
    background-color: $black;
    left: 0;
    position: absolute;
    top: 100%;
    width: 100%;
    z-index: 999;

    .dropdown-button__body,
    .header__link {
      color: $white;
    }
  }
}
